.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding-inline: 50px;
  display: flex;
}

.header__logo-link {
  width: 40px;
  height: 40px;
  display: flex;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
}

.is-active, .nav__link:hover {
  color: #00acdc;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  transition: all .3s;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

* {
  box-sizing: border-box;
  margin: 0;
}

.card {
  border: 1px solid #f3f3f3;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  display: block;
}

.card__title {
  color: #060b35;
  width: 100%;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product {
  color: #616070;
  width: 100%;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 16px 0 0;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 18px;
  margin: 24px 0 0;
  line-height: 18px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__link {
  text-align: center;
  text-transform: uppercase;
  color: #f3f3f3;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
  bottom: 1px;
}

.card__link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover__title {
  color: #34568b;
}

.reviews-text {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.reviews-stars {
  display: flex;
}

.stars-item {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: flex;
}

.stars-item:not(:last-child) {
  margin-right: 4px;
}

.stars-item--not-active {
  background-image: url("star.822d561e.svg");
}

